/* src/Christmas.css */

/* Intro Animation */
.intro-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeOut 1s ease-out 3s forwards;
}

.intro-content {
  display: flex;
  align-items: center;
  color: white;
  text-shadow: 0 0 20px rgba(255, 0, 0, 0.8);
}

.intro-text {
  font-size: 4rem;
  font-weight: bold;
  font-family: "Mountains of Christmas", cursive;
  animation: zoomIn 2s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
  opacity: 0;
}

.intro-icon {
  font-size: 4rem;
  margin: 0 20px;
  animation: popIn 1s cubic-bezier(0.34, 1.56, 0.64, 1) 0.5s forwards;
  opacity: 0;
  transform: scale(0);
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.5) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes popIn {
  from {
    opacity: 0;
    transform: scale(0) rotate(-45deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
