/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;


/* Import Premium Fonts */
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mountains+of+Christmas:wght@400;700&display=swap");

:root {
  --font-primary: 'Outfit', sans-serif;
}

body {
  font-family: var(--font-primary);
  @apply bg-gray-50 text-gray-900 antialiased overflow-x-hidden;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-transparent;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 rounded-full hover:bg-gray-400 transition-colors;
}

.dark ::-webkit-scrollbar-thumb {
  @apply bg-slate-700 hover:bg-slate-600;
}

@layer components {
  /* Glassmorphism Utilities */
  .glass {
    @apply bg-white/70 backdrop-blur-lg border border-white/20 shadow-xl;
  }
  
  .glass-dark {
    @apply dark:bg-slate-900/70 dark:backdrop-blur-lg dark:border-white/10 dark:shadow-xl;
  }

  .glass-card {
    @apply glass glass-dark rounded-2xl transition-all duration-300 hover:shadow-2xl hover:-translate-y-1;
  }

  /* Premium Button */
  .btn-premium {
    @apply relative overflow-hidden px-6 py-3 rounded-xl font-bold text-white shadow-lg transition-all duration-300 hover:shadow-indigo-500/30 hover:scale-105 active:scale-95;
    background: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  }

  .btn-premium::before {
    content: '';
    @apply absolute inset-0 bg-white/20 translate-y-full transition-transform duration-300;
  }

  .btn-premium:hover::before {
    @apply translate-y-0;
  }

  /* Section Headers */
  .section-title {
    @apply text-3xl md:text-4xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-gray-900 to-gray-600 dark:from-white dark:to-gray-400 mb-2 text-center;
  }
  
  .section-subtitle {
    @apply text-lg text-gray-500 dark:text-gray-400 text-center mb-10 max-w-2xl mx-auto;
  }
}

/* Animations */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

@keyframes glow {
  0%, 100% { opacity: 0.5; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.1); }
}

.animate-glow {
  animation: glow 3s ease-in-out infinite;
}

/* Utility for seasonal fonts */
.font-gowun-dodum {
  font-family: "Gowun Dodum", sans-serif;
}

/* Brawl Stars-inspired cheering animation styles */
.cheering-character {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: characterJump 2s infinite;
}

@keyframes characterJump {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.character-body {
  font-size: 4rem;
  filter: drop-shadow(2px 2px 2px rgba(0,0,0,0.2));
  z-index: 2;
}

.character-hood {
  position: relative;
  z-index: 1;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
}

.cheering-sign {
  background: linear-gradient(135deg, #FFD700, #FFA500);
  border: 3px solid #FF8C00;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0,0,0,0.3);
  animation: signShake 0.8s infinite alternate;
}

@keyframes signShake {
  0% { transform: rotate(-5deg); }
  100% { transform: rotate(5deg); }
}

.waving-hand {
  animation: handWave 1.5s infinite;
}

@keyframes handWave {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(20deg); }
  50% { transform: rotate(-15deg); }
  75% { transform: rotate(10deg); }
}

