/* src/NewYear.css */

/* New Year Intro Animation */
.ny-intro-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: nyFadeOut 1s ease-out 3s forwards;
}

.ny-intro-content {
  display: flex;
  align-items: center;
  color: #ffd700; /* Gold */
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.8);
}

.ny-intro-text {
  font-size: 4rem;
  font-weight: bold;
  font-family: "Mountains of Christmas", cursive;
  animation: nyZoomIn 2s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
  opacity: 0;
}

.ny-intro-icon {
  font-size: 4rem;
  margin: 0 20px;
  animation: nyPopIn 1s cubic-bezier(0.34, 1.56, 0.64, 1) 0.5s forwards;
  opacity: 0;
  transform: scale(0);
}

@keyframes nyZoomIn {
  from {
    opacity: 0;
    transform: scale(0.5) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes nyPopIn {
  from {
    opacity: 0;
    transform: scale(0) rotate(-45deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes nyFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}

/* New Year Ringing Bell Effect */
@keyframes ring-bell-animation {
  0% {
    opacity: 0;
    transform: translateY(-100%) scale(0.5) rotate(0deg);
  }
  10% {
    opacity: 1;
    transform: translateY(0) scale(1) rotate(0deg);
  }
  20%, 40%, 60%, 80% {
    transform: rotate(15deg);
  }
  30%, 50%, 70%, 90% {
    transform: rotate(-15deg);
  }
  100% {
    opacity: 0;
    transform: translateY(-20%) scale(1.2) rotate(0deg);
  }
}

.ringing-bell {
  position: fixed;
  top: 15%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 6rem;
  z-index: 9999;
  pointer-events: none;
  filter: drop-shadow(0 0 20px rgba(255, 215, 0, 0.6));
}

.animate-ring {
  animation: ring-bell-animation 4s ease-in-out forwards;
}
